import tuiActionsheet from '@/components/tui-actionsheet/tui-actionsheet.vue'
export default {
	components: {
		tuiActionsheet
	},
	data() {
		return {
			userInfo: {
				uPic: "http://qakj5dvcb.bkt.clouddn.com/static/logo.png",
				name: "Still walking",
				age: 24,
				gender: "女",
				tel: "187****1906",
				city: "北京市-北京市-东城区"
			},
			value: [],
			showActionSheet: false,
			tips: "选择您的性别",
			itemList: [{
				text: "男",
				color: "#2B2B2B"
			}, {
				text: "女",
				color: "#2B2B2B"
			}, {
				text: "不公开",
				color: "#2B2B2B"
			}],
			uploadImg: "", // 上传的头像地址
		}
	},
	onLoad(options) {
		if (options.src) this.userInfo.uPic = options.src;
		if (options.tem) this.uploadImg = options.tem;
	},
	methods: {
		addressPickerChange(e) {
			this.userInfo.city = e.detail.value.join("-");
		},
		chooseImage() {
			// uni.chooseImage({
			// 	count: 1,
			// 	sizeType: ['original', 'compressed'],
			// 	sourceType: ['album', 'camera'],
			// 	success: res => {
			// 		const tempFilePaths = res.tempFilePaths[0];
			// 		uni.navigateTo({
			// 			url: `./cropper?src=${tempFilePaths}`
			// 		})
			// 	}
			// });

			this.$http.uploadImage(1, (res, tem) => {
				if (res.code == 1) {
					uni.navigateTo({
						url: `./cropper?src=${tem}`
					})
				} else {
					this.$common.errorToShow(res.msg);
				}
			})


		},
		itemClick(e) {
			const index = e.index;
			this.userInfo.gender = this.itemList[index].text;
			this.closeActionSheet();
		},
		openActionSheet() {
			this.showActionSheet = true
		},
		closeActionSheet() {
			this.showActionSheet = false
		},
	}
}
